<template>
  <div class = "stage-root" v-bind:class= "{inView : showStage, outOfView : !showStage}" >
    <div class = "statBox">
      <transition name = "list">
        <div class = "stat loadedTitle" v-show = "showStage"> Viewing - test.obj</div>
      </transition> 
        <div class = "btn-download" v-show = "showStage"> Download .OBJJS</div>
    </div>

    <div class = "stage"> </div>

    <div class = "statBox">
      <transition-group name="list" tag="div">
        <div class = "stat" v-show = "showStage" v-for = "(statValue, statName) in stats" v-bind:key="statName"> {{statName}} - {{statValue}}</div>
      </transition-group>
    </div>
  </div>
</template>


<script>
export default {
  name: 'stage',

  created : function(){
    var me = this
     this.$parent.bus.$on('toggleCentered', function(show){
       me.showStage = !show
     })
  },
  data () {
    return {
      showStage : false,
      stats : {
        vertices : 152,
        normals : 104,
        uvs : 104,
        faces : 100
      }
    }
  }
}
</script>

<style lang = "stylus">

$stageWidth = 800px
$offset = 600px
$statHeight = 40px

.inView {
  width 800px
}

.outOfView {
  width 0px
}

$titleHeight = 40px

.loadedTitle{
  margin-bottom 15px
  width 200px
  font-weight bold
}

.stage{
    background-color white
    height 600px
    margin-bottom 15px
}

.stage-root{
    margin-top 40px
    height 750px
    margin-left 30px
    transition opacity .5s ease, width .5s ease
    overflow-x hidden
    overflow-y hidden
}

.btn-download{
  background-color #ff0035
  color white
  height $statHeight
  width 150px
  float left
  padding 5px 15px
  line-height $statHeight
  cursor pointer
}

.statBox{
  width 800px
  overflow-x hidden
  overflow-y hidden
  flex-shrink 0
  .stat{
    float left
    background-color white
  }
}

.stat{
  margin-right 15px
  height $statHeight
  line-height $statHeight
  padding 5px 15px
  font-size 20px
}

.list-item {
  display: inline-block;
}
.list-enter-active, .list-leave-active {
  transition: all .5s;
}
.list-enter, .list-leave-to /* .list-leave-active for <2.1.8 */ {
  opacity: 0;
  transform: translateY(5px);
}

</style>


