<template>
  <div class="panel">

  <div class = "pane-root" v-bind:class = "{pane_status_centered : this.paneCentered, pane_status_left : !this.paneCentered}"> 
    <div class = "title-root"> 
    TFWGF
    </div>

    <transition name = "verticalSquish" mode="out-in"> 
    <div class = "descriptor-root" v-show="paneCentered"> 
    The friendly web graphics file converter
    </div>
    </transition>
    
    <transition-group name = "fade" mode="in-out" tag = "div">
    <div class = "library" v-bind:key = "'lib'" v-show="!paneCentered">
      <h2> Library</h2>
      <div v-for = "file in assets"> 
        {{file.name}}
      </div>
    </div>

    <div class = "conversion-table-root" v-bind:key="'table'" v-show="paneCentered">
    <div class = "row"> 
      <div class = "side"> <div class = "itemType"> OBJ </div> </div>
      <div class = "divider"> </div>
      <div class = "side"> <div class = "itemType"> JSON</div> </div>
    </div>
    <div class = "row"> 
      <div class = "side">  <div class = "itemType"> Image</div></div>
       <div class = "divider"> </div>
      <div class = "side">  <div class = "itemType"> Base64</div></div>
    </div>
    </div>
      </transition-group>
 
<!--onchange="generate(event)!-->
    <div class = "btn-upload-root"> 
      <input class = "inputfile" name = "file" id = "file" type="file" value="Upload and Convert" v-on:change= "generate"/>
      <label for="file">Upload and Convert </label>
    </div>
    
  </div>
  </div>
</template>

<script>
export default {
  name: 'panel',
  methods : {
    toggleView : function(){
      if(this.paneCentered){
        this.paneCentered = !this.paneCentered
        this.$parent.bus.$emit('toggleCentered',this.paneCentered)
      }
    },
    generate : function(e){
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length)
        return;
      
      console.log('Received File:' + e.target.files)
      this.$parent.bus.$emit('event_loadedFile',e.target.files)
      this.toggleView()
    }
  },
  props : ["assets"],
  data () {
    return {
      paneCentered : true,
      library : [
        {"name" : "test.obj"}
      ]
    }
  }
}
</script>


<style lang = "stylus">

.inputfile {
  width 0.1px
  height 0.1px
  opacity 0
  overflow hidden
  position absolute
  z-index -1
}

.inputfile + label {
  font-size: 1em
  font-weight: 200
  color: white
  background-color: black
  display: inline-block
  width 100%
  outline 5px solid black
  cursor pointer
}

.inputfile:focus + label,
.inputfile + label:hover {
    background-color #FFF
    color black
}

$titleHeight = 60px
$darkText = #444
$lightText = #eee
$outlinestrength = 10px
$accentColor = #FF0035
$leftMargin = 50px
$paneWidth = 400px
$offset = $paneWidth*2

.pane_status_centered {

}

.pane_status_left{

}

.library{
  padding-left 25px
  padding-right 25px
  font-size 20px

  h2{
    border-bottom 1px solid rgba(0,0,0,.2)
  }
}

.pane-root{
  width $paneWidth
  height 500px
  background-color #eee
  outline 5px solid black
  box-shadow 0px 0px 15px 5px
  transition left .5s ease
  top 0
  bottom 0
  position relative

  .title-root{
    color white
    font-weight bold
    background-color black
    height $titleHeight
    line-height $titleHeight
    font-size $titleHeight*.65
    text-align center
    text-shadow: 0px 3px 0px #555 
  }

  .descriptor-root{
    $descriptorHeight = 60px
    box-shadow 0px 3px 5px 1px rgba(0,0,0,.2)
    height $descriptorHeight
    text-align center
    font-size 1.2em
    color $lightText
    background-color $accentColor
    line-height $descriptorHeight
  }

  .verticalSquish-enter-active {
    transition: all .5s ease
  }
  .verticalSquish-leave-active {
    transition: all .5s ease
  }
  .verticalSquish-enter, .verticalSquish-leave-to{
    height 0px
  }


  .row:first-child{
    border-bottom 2px solid rgba(0,0,0,.1)
  }

  .conversion-table-root{
    margin-top 50px
    height 200px
    width 100%
    $rowHeight = 100px
    padding 0px 40px
    box-sizing border-box

    .row{
      display flex
      flex-grow 1
      justify-content center
      font-size 20px
      align-items stretch
      width 100%
      height $rowHeight
      line-height $rowHeight
      margin-bottom 15px
      padding-bottom 15px
      text-align center

      .side{
        width 45%

        .itemType{
          font-size 30px
          font-weight bold
          color $darkText
        }
      }
      .divider{
        width 10%
        background-image url("../assets/arrow_right.png")
        background-repeat no-repeat
        background-position center
        background-size 90%
      }
    }
  }

  .btn-upload-root{
    $btnHeight = 70px
    width 200px
    color white
    background-color black
    height $btnHeight
    position absolute
    bottom $btnHeight * -.5
    left 0
    right 0
    margin auto
    top 1

    text-align center
    line-height $btnHeight
    cursor pointer
  }

}


.fade-enter-active {
  transition: all .5s ease;
}
.fade-leave-active {
  transition: all .5s ease
}
.fade-enter, .fade-leave-to{
  opacity 0
  height 0
}

</style>



