<template>
  <div id="app">
    <div class = "root">
      <panel :assets = "assetStore"></panel>
      <stage> </stage>
    </div>
  </div>
</template>

<script>

import Panel from './components/Panel'
import Stage from './components/Stage'
import Vue from 'vue'

export default {
  name: 'app',
  components: {
    Panel, Stage
  },
  data (){
    return {
      bus : new Vue(),
      assetStore : [
        {
          name : "sample.png",
        },
        {
          name : "sample.obj",
        },
      ]
    }
  },
  methods : {
    loadedFile : function(fileList){      
        var curFile = fileList[0]
        //YUSEF TODO - HERE WE NEED TO CONVERT AND INSERT OBJ DATA
        var newFile = {
          name : curFile.name,
          processedData : "MAGICHERE"
        }
        this.assetStore.splice(0,0,newFile)
    }
  },
  created : function(){
    var me = this
    this.bus.$on('event_loadedFile', function(file){
      me.loadedFile(file)
    })
  }
}

</script>

<style lang = "stylus">
$stripHeight = 25px

#app {
  height 100%
}

body{
  padding 0px 0px
  margin 0px 0px
  background #262626
  background radial-gradient(ellipse at center, #262626 0%,#000000 100%)
  font-family 'Poppins', sans-serif
} 

.root{
  display flex
  justify-content center
  align-items center
}

.strip-top {
  height $stripHeight
  top 0px
  position absolute
  width 100%
  background-color black
}

</style>



